<template>
	<view class="container">
		<image src="http://localhost:3000/public/images/bj/bg1.png" mode="widthFix" class="bg1"></image>
		<image src="http://localhost:3000/public/images/bj/bg2.png" mode="widthFix" class="bg2"></image>
		<view class="form-box">
			<view class="title">用户登录</view>
			<uni-forms ref="loginForm" :rules="rules" :modelValue="loginForm">
				<uni-forms-item name='username' class="input-box">
					<input type="text" label-width='' placeholder="请输入用户名" class="input" v-model="loginForm.username">
				</uni-forms-item>
				<uni-forms-item name='password' class="input-box">
					<input class="input" placeholder="请输入密码" :password="showPassword" v-model="loginForm.password" />
					<uni-icons :type="showPassword?'eye-filled':'eye-slash-filled'" :color="showPassword?'#009000':'#999'" size="25" class="icon" @click="changePassword" v-show="loginForm.password"></uni-icons>
				</uni-forms-item>
			</uni-forms>
			<button class="btn" @click="clickLogin">登录</button>
			<!-- <view class="tip1">忘记密码？</view> -->
			<view class="tip2" @click="toRegister">还没有账号？<text>马上注册</text></view>
		</view>
	</view>
</template>

<script>
	import {
		mapMutations
	} from 'vuex';
	export default {
		data() {
			return {
				loginForm: {
					username: '',
					password: ''
				},
				rules: { // 校验规则
					username: {
						rules: [{
							required: true,
							errorMessage: '用户名不能为空'
						}]
					},
					password: {
						rules: [{
							required: true,
							errorMessage: '密码不能为空'
						}]
					}
				},
				showPassword: true
			}
		},
		watch:{
			'loginForm.password':function(newVal,oldVal){
				if(!newVal){
					this.showPassword=true
				}
			}
		},
		methods: {
			...mapMutations(['setUserInfo', 'setToken']),
			changePassword: function() {
				this.showPassword = !this.showPassword;
			},
			clickLogin() {
				this.$refs.loginForm.validate().then(res => { //效验登录信息
					this.submitLogin()
				}).catch(err => {
					console.log('err', err);
				})
			},
			async submitLogin() { //发送登录接口请求
				const response = await this.$api.user.login(this.loginForm)
				if (response.data.code === 200) { //登录成功
					uni.showToast({
						title: response.data.message,
						icon: 'none',
						duration: 2000
					});
					// 保存token
					this.setToken(response.data.data)
					// 获取用户信息
					this.getUserInfo(response.data.data.userId)
					// 页面跳转
					let pages = getCurrentPages() 
					if(pages.length <= 1){
						uni.switchTab({
							url: '/pages/user/user'
						});
					}else{
						// 注意是length长度，所以要想得到上一页面的实例需要 -2 ,若要返回上上页面的实例就 -3，以此类推
						let prevPage = pages[pages.length -2] 
						console.log(prevPage)
						if(prevPage.route == 'pages/user/login'){
							uni.switchTab({
								url: '/pages/user/user'
							});
						}else{
							uni.navigateBack()
						}
					}
				} 
			},
			async getUserInfo(id) { //获取用户信息
				const response = await this.$api.user.getUserInfo(id)
				if (response.data.code === 200) {
					// 保存用户信息
					this.setUserInfo(response.data.data)
				}
			},
			toRegister(){ //跳转注册页
				uni.navigateTo({
					url:'/pages/user/register'
				})
			}
		}
	}
</script>

<style lang="scss">
	.container {
		width: 100%;
		min-height: 100vh;
		background-color: #fff;
		position: relative;

		.bg1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			z-index: 1;
		}

		.bg2 {
			position: absolute;
			bottom: 120upx;
			left: calc(50% - 250upx);
			width: 500upx;
			z-index: 1;
		}

		.form-box {
			position: absolute;
			top: calc(50% - 280upx);
			left: 0;
			z-index: 2;
			width: 100%;
			padding: 0 50upx;
			text-align: center;

			.title {
				font-size: 50upx;
				color: #009000;
				font-weight: bold;
				display: inline-block;
				margin-bottom: 20upx;
			}

			/deep/.uni-forms-item {
				width: 100%;
				margin-top: 40upx;
                position: relative;
				.input {
					width: 100%;
					height: 80upx;
					border: 1px solid #009000;
					border-radius: 8upx;
					text-align: left;
					padding: 0 30upx;
					font-size: 28upx;
				}
				.icon{
					position: absolute;
					top: calc(50% - 20upx);
					right: 20upx;
				}
			}

			.btn {
				width: 100%;
				height: 80upx;
				line-height: 80upx;
				background-color: #009000;
				border-radius: 50upx;
				color: #fff;
				margin-top: 50upx;
				font-size: 35upx;
			}

			.tip1 {
				font-size: 26upx;
				color: #009000;
				padding: 20upx 0;
				margin-top: 15upx;
			}

			.tip2 {
				font-size: 26upx;
				color: $uni-secondary-color;
				position: fixed;
				bottom: 15upx;
				left: 0;
				width: 100%;
				padding: 20upx 0;

				text {
					color: #009000;
				}
			}
		}
	}
</style>